/* Styling for the registration page */
.register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f4f9;
}

.register-box {
  width: 300px;
  padding: 30px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.input-group {
  margin-bottom: 15px;
}

.input-field {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.submit-btn {
  width: 100%;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: #45a049;
}

.success-message {
  color: green;
  text-align: center;
}

.error-message {
  color: red;
  text-align: center;
}
@media (max-width: 600px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    padding: 10px 5px;
  }

  .navbar-link {
    margin: 10px 0;
    font-size: 14px;
  }

  .navbar-user {
    font-size: 14px;
  }

  .logout-btn {
    margin-top: 10px;
    font-size: 14px;
  }
}

/* สำหรับแท็บเล็ต (max-width: 900px) */
@media (max-width: 900px) {
  .navbar {
    padding: 10px 15px;
  }

  .navbar-link {
    font-size: 15px;
    margin: 0 10px;
  }

  .navbar-user {
    font-size: 15px;
  }

  .logout-btn {
    font-size: 15px;
  }
}

/* สำหรับขนาดหน้าจอที่ใหญ่กว่า (Desktop) */
@media (min-width: 901px) {
  .navbar {
    padding: 10px 40px;
  }

  .navbar-link {
    font-size: 16px;
    margin: 0 20px;
  }

  .navbar-user {
    font-size: 16px;
  }

  .logout-btn {
    font-size: 16px;
  }
}