.home-container {
    text-align: center;
    padding: 20px;
  }
  
  .home-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .shops-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .shop-link {
    display: block;
    width: 200px;
    padding: 10px;
    background-color: #3498db;
    color: white;
    text-align: center;
    text-decoration: none;
    border-radius: 8px;
    font-size: 18px;
    transition: 0.3s;
  }
  
  .shop-link:hover {
    background-color: #2980b9;
  }
  @media (max-width: 600px) {
    .navbar {
      flex-direction: column;
      align-items: center;
      padding: 10px 5px;
    }
  
    .navbar-link {
      margin: 10px 0;
      font-size: 14px;
    }
  
    .navbar-user {
      font-size: 14px;
    }
  
    .logout-btn {
      margin-top: 10px;
      font-size: 14px;
    }
  }
  
  /* สำหรับแท็บเล็ต (max-width: 900px) */
  @media (max-width: 900px) {
    .navbar {
      padding: 10px 15px;
    }
  
    .navbar-link {
      font-size: 15px;
      margin: 0 10px;
    }
  
    .navbar-user {
      font-size: 15px;
    }
  
    .logout-btn {
      font-size: 15px;
    }
  }
  
  /* สำหรับขนาดหน้าจอที่ใหญ่กว่า (Desktop) */
  @media (min-width: 901px) {
    .navbar {
      padding: 10px 40px;
    }
  
    .navbar-link {
      font-size: 16px;
      margin: 0 20px;
    }
  
    .navbar-user {
      font-size: 16px;
    }
  
    .logout-btn {
      font-size: 16px;
    }
  }