.payment-container {
    padding: 20px;
    max-width: 600px;
    margin: auto;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  h1 {
    text-align: center;
  }
  
  .qr-container {
    text-align: center;
    margin: 20px 0;
  }
  
  .qr-code {
    margin-top: 10px;
  }
  
  .payment-proof {
    margin-top: 20px;
  }
  
  .payment-proof label {
    display: block;
    font-size: 16px;
    margin-bottom: 10px;
  }
  
  .payment-proof input {
    margin-bottom: 10px;
  }
  
  .proof-preview {
    margin-top: 10px;
    text-align: center;
  }
  
  .proof-image {
    max-width: 200px;
    margin-top: 10px;
    border-radius: 8px;
  }
  
  .empty-cart-message {
    text-align: center;
    color: #888;
    font-size: 16px;
  }
  @media (max-width: 600px) {
    .navbar {
      flex-direction: column;
      align-items: center;
      padding: 10px 5px;
    }
  
    .navbar-link {
      margin: 10px 0;
      font-size: 14px;
    }
  
    .navbar-user {
      font-size: 14px;
    }
  
    .logout-btn {
      margin-top: 10px;
      font-size: 14px;
    }
  }
  
  /* สำหรับแท็บเล็ต (max-width: 900px) */
  @media (max-width: 900px) {
    .navbar {
      padding: 10px 15px;
    }
  
    .navbar-link {
      font-size: 15px;
      margin: 0 10px;
    }
  
    .navbar-user {
      font-size: 15px;
    }
  
    .logout-btn {
      font-size: 15px;
    }
  }
  
  /* สำหรับขนาดหน้าจอที่ใหญ่กว่า (Desktop) */
  @media (min-width: 901px) {
    .navbar {
      padding: 10px 40px;
    }
  
    .navbar-link {
      font-size: 16px;
      margin: 0 20px;
    }
  
    .navbar-user {
      font-size: 16px;
    }
  
    .logout-btn {
      font-size: 16px;
    }
  }