/* Navbar.css */

/* การตั้งค่าพื้นฐาน */
.navbar {
  background-color: #28a745;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; /* ทำให้รายการภายในสามารถยืดหยุ่นได้ */
}

.navbar-link {
  color: white;
  text-decoration: none;
  margin: 0 15px;
  font-size: 16px;
  font-weight: 500;
}

.navbar-link:hover {
  color: #f1f1f1;
}

.navbar-user {
  color: white;
  font-size: 16px;
  font-weight: 500;
}

.logout-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 5px 10px;
  font-size: 16px;
  cursor: pointer;
  margin-left: 15px;
  border-radius: 4px;
}

.logout-btn:hover {
  background-color: #c0392b;
}

/* สำหรับขนาดหน้าจอเล็ก (มือถือ) */
@media (max-width: 600px) {
  .navbar {
    flex-direction: column;
    align-items: center;
  }

  .navbar-link {
    margin: 10px 0;
    font-size: 14px;
  }

  .navbar-user {
    font-size: 14px;
  }

  .logout-btn {
    margin-top: 10px;
    font-size: 14px;
  }
}

/* สำหรับขนาดหน้าจอกลาง (แท็บเล็ต) */
@media (max-width: 900px) {
  .navbar {
    padding: 10px 15px;
  }

  .navbar-link {
    font-size: 15px;
    margin: 0 10px;
  }

  .navbar-user {
    font-size: 15px;
  }

  .logout-btn {
    font-size: 15px;
  }
}

/* ปุ่ม Login/Register */
.login-register-btn {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
  margin-left: 15px;
  border-radius: 4px;
}

.login-register-btn:hover {
  background-color: #2980b9;
}
