.menu-container {
    padding: 20px;
  }
  
  .menu-items {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .menu-item {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .item-info {
    display: flex;
    justify-content: space-between;
  }
  
  .addon-container {
    margin-top: 10px;
  }
  
  .addon-label {
    margin-right: 10px;
  }
  
  .add-button {
    background-color: #4CAF50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-button:hover {
    background-color: #45a049;
  }
  
  .go-to-cart {
    margin-top: 20px;
    background-color: #008CBA;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .go-to-cart:hover {
    background-color: #007B9A;
  }
  @media (max-width: 600px) {
    .navbar {
      flex-direction: column;
      align-items: center;
      padding: 10px 5px;
    }
  
    .navbar-link {
      margin: 10px 0;
      font-size: 14px;
    }
  
    .navbar-user {
      font-size: 14px;
    }
  
    .logout-btn {
      margin-top: 10px;
      font-size: 14px;
    }
  }
  
  /* สำหรับแท็บเล็ต (max-width: 900px) */
  @media (max-width: 900px) {
    .navbar {
      padding: 10px 15px;
    }
  
    .navbar-link {
      font-size: 15px;
      margin: 0 10px;
    }
  
    .navbar-user {
      font-size: 15px;
    }
  
    .logout-btn {
      font-size: 15px;
    }
  }
  
  /* สำหรับขนาดหน้าจอที่ใหญ่กว่า (Desktop) */
  @media (min-width: 901px) {
    .navbar {
      padding: 10px 40px;
    }
  
    .navbar-link {
      font-size: 16px;
      margin: 0 20px;
    }
  
    .navbar-user {
      font-size: 16px;
    }
  
    .logout-btn {
      font-size: 16px;
    }
  }